const form = () => {
    const formBlock= document.querySelector("form"),
          inputs = document.querySelectorAll("input");

    const messages = {
        loading: 'Загрузка...',
        success: 'Спасибо! Скоро я с вами свяжусь',
        failure: 'Что-то пошло не так...'
    };

    const postData = async (url, data) => {
        document.querySelector('.spiner').textContent = messages.loading;
        let res = await fetch(url, {
            method: "POST",
            body: data
        });

        return await res.text();
    };

    const clearInputs = () => {
        inputs.forEach(item => {
            item.value = '';
        });
    }

    formBlock.addEventListener('submit', (e) => {
        e.preventDefault();

        let statusMessage = document.createElement('div');
        statusMessage.classList.add('spiner');
        formBlock.appendChild(statusMessage);

        const formData = new FormData(formBlock);

        postData('assets/server.php', formData)
            .then(res => {
                console.log(res);
                statusMessage.textContent = messages.success;
            })
            .catch(() => statusMessage.textContent = messages.failure)
            .finally(() => {
                clearInputs();
                setTimeout(() => {
                    statusMessage.remove();
                }, 5000);
            });
    });
};

export default form;